import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Container,
  Typography,
  Button,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Card,
  CardContent,
  Stack,
  CssBaseline,
  Alert,
  CircularProgress
} from '@mui/material';
import {
  Save as SaveIcon,
  ArrowBack as ArrowBackIcon
} from '@mui/icons-material';
import AppTheme from '../shared-theme/AppTheme';
import ColorModeSelect from '../shared-theme/ColorModeSelect';
import { userAPI, authUtils } from '../../services/api';

export default function SignUp(props) {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    phone: '',
    gender: '',
    dormitory: '',
    studentId: ''
  });
  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);

  const dormitories = ['1', '2', '3', '4', '5', '6', '7'];
  const genders = ['', ''];

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    
    //  
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const validateForm = () => {
    const newErrors = {};

    if (!formData.name.trim()) {
      newErrors.name = ' ';
    }

    if (!formData.email.trim()) {
      newErrors.email = ' ';
    } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
      newErrors.email = '   ';
    }

    if (!formData.password.trim()) {
      newErrors.password = ' ';
    } else if (formData.password.length < 6) {
      newErrors.password = ' 6  ';
    }

    if (!formData.phone.trim()) {
      newErrors.phone = ' ';
    } else if (!/^010-\d{4}-\d{4}$/.test(formData.phone)) {
      newErrors.phone = '    (010-0000-0000)';
    }

    if (!formData.gender) {
      newErrors.gender = ' ';
    }

    if (!formData.dormitory) {
      newErrors.dormitory = ' ';
    }

    if (!formData.studentId.trim()) {
      newErrors.studentId = ' ';
    } else if (!/^\d{8}$/.test(formData.studentId)) {
      newErrors.studentId = '8  ';
    }

    return newErrors;
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    
    const newErrors = validateForm();
    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors);
      return;
    }

    setLoading(true);
    setErrors({});

    try {
      //       
      const response = await fetch(`http://172.29.122.76:3001/users?email=${formData.email}`);
      const existingUsers = await response.json();
      
      if (existingUsers.length > 0) {
        setErrors({ email: '  ' });
        setLoading(false);
        return;
      }

      //  
      const newUser = await userAPI.register({
        ...formData,
        createdAt: new Date().toISOString()
      });

      console.log(' :', newUser);
      setSuccess(true);
      
      // 2    
      setTimeout(() => {
        navigate('/signin', { 
          state: { 
            message: ' . .',
            email: formData.email 
          }
        });
      }, 2000);

    } catch (error) {
      console.error(' :', error);
      setErrors({ submit: '   .  .' });
    } finally {
      setLoading(false);
    }
  };

  const handleCancel = () => {
    navigate('/signin');
  };

  if (success) {
    return (
      <AppTheme {...props}>
        <CssBaseline enableColorScheme />
        <Container maxWidth="md" sx={{ py: 8 }}>
          <Box sx={{ textAlign: 'center' }}>
            <Alert severity="success" sx={{ mb: 3 }}>
                !
            </Alert>
            <Typography variant="h6">
                  ...
            </Typography>
          </Box>
        </Container>
      </AppTheme>
    );
  }

  return (
    <AppTheme {...props}>
      <CssBaseline enableColorScheme />
      <ColorModeSelect sx={{ position: 'fixed', top: '1rem', right: '1rem' }} />
      
      <Container maxWidth="md" sx={{ py: 8 }}>
        {/*  */}
        <Box sx={{ mb: 4 }}>
          <Button
            startIcon={<ArrowBackIcon />}
            onClick={handleCancel}
            sx={{ mb: 2 }}
          >
             
          </Button>
          <Typography variant="h3" component="h1" gutterBottom>
             
          </Typography>
          <Typography variant="body1" color="text.secondary">
            UNIST     
          </Typography>
        </Box>

        {/*   */}
        <Card>
          <CardContent sx={{ p: 4 }}>
            {errors.submit && (
              <Alert severity="error" sx={{ mb: 3 }}>
                {errors.submit}
              </Alert>
            )}

            <Box component="form" onSubmit={handleSubmit} noValidate>
              <Stack spacing={3}>
                {/*  */}
                <TextField
                  name="name"
                  label=""
                  value={formData.name}
                  onChange={handleChange}
                  error={!!errors.name}
                  helperText={errors.name}
                  fullWidth
                  required
                  placeholder=""
                />

                {/*  */}
                <TextField
                  name="email"
                  label=""
                  type="email"
                  value={formData.email}
                  onChange={handleChange}
                  error={!!errors.email}
                  helperText={errors.email}
                  fullWidth
                  required
                  placeholder="student@unist.ac.kr"
                />

                {/*  */}
                <TextField
                  name="password"
                  label=""
                  type="password"
                  value={formData.password}
                  onChange={handleChange}
                  error={!!errors.password}
                  helperText={errors.password}
                  fullWidth
                  required
                  placeholder="6  "
                />

                {/*  */}
                <TextField
                  name="phone"
                  label=""
                  value={formData.phone}
                  onChange={handleChange}
                  error={!!errors.phone}
                  helperText={errors.phone}
                  fullWidth
                  required
                  placeholder="010-0000-0000"
                />

                {/*  */}
                <FormControl fullWidth error={!!errors.gender} required>
                  <InputLabel></InputLabel>
                  <Select
                    name="gender"
                    value={formData.gender}
                    onChange={handleChange}
                    label=""
                  >
                    {genders.map((gender) => (
                      <MenuItem key={gender} value={gender}>
                        {gender}
                      </MenuItem>
                    ))}
                  </Select>
                  {errors.gender && (
                    <Typography variant="caption" color="error" sx={{ mt: 0.5, ml: 2 }}>
                      {errors.gender}
                    </Typography>
                  )}
                </FormControl>

                {/*  */}
                <FormControl fullWidth error={!!errors.dormitory} required>
                  <InputLabel> </InputLabel>
                  <Select
                    name="dormitory"
                    value={formData.dormitory}
                    onChange={handleChange}
                    label=" "
                  >
                    {dormitories.map((dorm) => (
                      <MenuItem key={dorm} value={dorm}>
                        {dorm}
                      </MenuItem>
                    ))}
                  </Select>
                  {errors.dormitory && (
                    <Typography variant="caption" color="error" sx={{ mt: 0.5, ml: 2 }}>
                      {errors.dormitory}
                    </Typography>
                  )}
                </FormControl>

                {/*  */}
                <TextField
                  name="studentId"
                  label=""
                  value={formData.studentId}
                  onChange={handleChange}
                  error={!!errors.studentId}
                  helperText={errors.studentId}
                  fullWidth
                  required
                  placeholder="20231234"
                />

                {/*  */}
                <Stack direction="row" spacing={2} justifyContent="flex-end">
                  <Button
                    type="button"
                    variant="outlined"
                    onClick={handleCancel}
                    size="large"
                    disabled={loading}
                  >
                    
                  </Button>
                  <Button
                    type="submit"
                    variant="contained"
                    startIcon={loading ? <CircularProgress size={20} /> : <SaveIcon />}
                    size="large"
                    disabled={loading}
                  >
                    {loading ? ' ...' : ''}
                  </Button>
                </Stack>
              </Stack>
            </Box>
          </CardContent>
        </Card>

        {/*  */}
        <Box sx={{ mt: 4, p: 3, bgcolor: 'primary.50', borderRadius: 2 }}>
          <Typography variant="h6" gutterBottom>
              
          </Typography>
          <Typography variant="body2" component="div">
             UNIST   <br/>
                <br/>
                    <br/>
                       
          </Typography>
        </Box>
      </Container>
    </AppTheme>
  );
}