import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Container,
  Typography,
  Button,
  Card,
  CardContent,
  CardActions,
  Grid,
  Chip,
  TextField,
  InputAdornment,
  Pagination,
  Stack,
  CssBaseline,
  Tabs,
  Tab,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Alert,
  CircularProgress
} from '@mui/material';
import {
  Search as SearchIcon,
  Add as AddIcon,
  Person as PersonIcon,
  Schedule as ScheduleIcon,
  Phone as PhoneIcon,
  LocationOn as LocationIcon,
  AttachMoney as MoneyIcon,
  Group as GroupIcon
} from '@mui/icons-material';
import AppTheme from '../shared-theme/AppTheme';
import ColorModeSelect from '../shared-theme/ColorModeSelect';
import { postAPI, miscAPI, authUtils } from '../../services/api';

export default function BoardList() {
  const navigate = useNavigate();
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [searchTerm, setSearchTerm] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [tabValue, setTabValue] = useState(0); // 0: , 1: , 2: 
  const [buildingFilter, setBuildingFilter] = useState('');
  const [buildings, setBuildings] = useState([]);
  const postsPerPage = 6;

  //   
  const currentUser = authUtils.getUser();

  //  
  useEffect(() => {
    loadPosts();
    loadBuildings();
  }, [tabValue, buildingFilter]);

  const loadPosts = async () => {
    try {
      setLoading(true);
      const params = {};
      
      //   
      if (tabValue === 1) params.type = 'help_request';
      if (tabValue === 2) params.type = 'help_offer';
      
      //  
      if (buildingFilter) {
        params.fromBuilding = buildingFilter;
      }

      const data = await postAPI.getAllPosts(params);
      setPosts(data);
    } catch (err) {
      setError('   .');
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  const loadBuildings = async () => {
    try {
      const data = await miscAPI.getBuildings();
      setBuildings(data);
    } catch (err) {
      console.error('   .');
    }
  };

  //  
  const filteredPosts = posts.filter(post =>
    post.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
    post.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
    post.authorName.toLowerCase().includes(searchTerm.toLowerCase()) ||
    post.fromBuilding.includes(searchTerm) ||
    post.toBuilding.includes(searchTerm)
  );

  // 
  const totalPages = Math.ceil(filteredPosts.length / postsPerPage);
  const startIndex = (currentPage - 1) * postsPerPage;
  const currentPosts = filteredPosts.slice(startIndex, startIndex + postsPerPage);

  const handleSearch = (event) => {
    setSearchTerm(event.target.value);
    setCurrentPage(1);
  };

  const handlePageChange = (event, value) => {
    setCurrentPage(value);
  };

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
    setCurrentPage(1);
  };

  const handlePostClick = (postId) => {
    navigate(`/board/${postId}`);
  };

  const handleWriteClick = () => {
    if (!currentUser) {
      alert(' .');
      navigate('/signin');
      return;
    }
    navigate('/board/write');
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'open':
        return 'success';
      case 'matched':
        return 'warning';
      case 'completed':
        return 'default';
      default:
        return 'default';
    }
  };

  const getStatusText = (status) => {
    switch (status) {
      case 'open':
        return '';
      case 'matched':
        return '';
      case 'completed':
        return '';
      default:
        return '';
    }
  };

  const getTypeText = (type) => {
    return type === 'help_request' ? '' : '';
  };

  const getTypeColor = (type) => {
    return type === 'help_request' ? 'error' : 'primary';
  };

  if (loading) {
    return (
      <AppTheme>
        <CssBaseline enableColorScheme />
        <Container maxWidth="lg" sx={{ py: 8, textAlign: 'center' }}>
          <CircularProgress size={60} />
          <Typography variant="h6" sx={{ mt: 2 }}>
              ...
          </Typography>
        </Container>
      </AppTheme>
    );
  }

  return (
    <AppTheme>
      <CssBaseline enableColorScheme />
      <ColorModeSelect sx={{ position: 'fixed', top: '1rem', right: '1rem' }} />
      
      <Container maxWidth="lg" sx={{ py: 8 }}>
        {/*   */}
        <Box sx={{ mb: 4 }}>
          <Typography variant="h3" component="h1" gutterBottom>
               
          </Typography>
          <Typography variant="body1" color="text.secondary" sx={{ mb: 3 }}>
               ,      !
          </Typography>

          {error && (
            <Alert severity="error" sx={{ mb: 3 }}>
              {error}
            </Alert>
          )}

          {/*   */}
          <Tabs 
            value={tabValue} 
            onChange={handleTabChange} 
            sx={{ mb: 3 }}
            variant="scrollable"
            scrollButtons="auto"
          >
            <Tab label="" />
            <Tab label=" " />
            <Tab label=" " />
          </Tabs>
          
          {/*    */}
          <Stack 
            direction={{ xs: 'column', sm: 'row' }} 
            spacing={2} 
            alignItems={{ xs: 'stretch', sm: 'center' }}
            justifyContent="space-between"
            sx={{ mb: 3 }}
          >
            <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2} sx={{ flex: 1 }}>
              <TextField
                placeholder=", ,  ..."
                value={searchTerm}
                onChange={handleSearch}
                sx={{ minWidth: 300 }}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <SearchIcon />
                    </InputAdornment>
                  ),
                }}
              />
              <FormControl sx={{ minWidth: 120 }}>
                <InputLabel> </InputLabel>
                <Select
                  value={buildingFilter}
                  onChange={(e) => setBuildingFilter(e.target.value)}
                  label=" "
                >
                  <MenuItem value=""></MenuItem>
                  {buildings.map((building) => (
                    <MenuItem key={building.id} value={building.name}>
                      {building.name} ({building.type})
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Stack>
            
            <Button
              variant="contained"
              startIcon={<AddIcon />}
              onClick={handleWriteClick}
              sx={{ minWidth: 120 }}
            >
               
            </Button>
          </Stack>
        </Box>

        {/*   */}
        <Grid container spacing={3}>
          {currentPosts.map((post) => (
            <Grid item xs={12} md={6} key={post.id}>
              <Card 
                sx={{ 
                  height: '100%', 
                  display: 'flex', 
                  flexDirection: 'column',
                  cursor: 'pointer',
                  transition: 'all 0.2s',
                  '&:hover': {
                    transform: 'translateY(-2px)',
                    boxShadow: 4,
                  }
                }}
                onClick={() => handlePostClick(post.id)}
              >
                <CardContent sx={{ flexGrow: 1, p: 3 }}>
                  {/*   */}
                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 2, gap: 1 }}>
                    <Chip 
                      label={getTypeText(post.type)} 
                      color={getTypeColor(post.type)}
                      size="small"
                    />
                    <Chip 
                      label={getStatusText(post.status)} 
                      color={getStatusColor(post.status)}
                      size="small"
                      variant="outlined"
                    />
                    <Box sx={{ ml: 'auto', display: 'flex', alignItems: 'center', color: 'text.secondary' }}>
                      <GroupIcon sx={{ fontSize: 16, mr: 0.5 }} />
                      <Typography variant="caption">
                        {post.applicants?.length || 0} 
                      </Typography>
                    </Box>
                  </Box>
                  
                  <Typography variant="h6" component="h2" gutterBottom>
                    {post.title}
                  </Typography>

                  {/*   */}
                  {post.type === 'help_request' && (
                    <Box sx={{ mb: 2 }}>
                      <Stack direction="row" spacing={1} alignItems="center" sx={{ mb: 1 }}>
                        <LocationIcon sx={{ fontSize: 16, color: 'text.secondary' }} />
                        <Typography variant="body2" color="text.secondary">
                          {post.fromBuilding} {post.fromRoom}  {post.toBuilding} {post.toRoom}
                        </Typography>
                      </Stack>
                      <Stack direction="row" spacing={2}>
                        <Box sx={{ display: 'flex', alignItems: 'center' }}>
                          <ScheduleIcon sx={{ fontSize: 16, mr: 0.5, color: 'text.secondary' }} />
                          <Typography variant="body2" color="text.secondary">
                            {post.moveDate} {post.moveTime}
                          </Typography>
                        </Box>
                        <Box sx={{ display: 'flex', alignItems: 'center' }}>
                          <MoneyIcon sx={{ fontSize: 16, mr: 0.5, color: 'text.secondary' }} />
                          <Typography variant="body2" color="text.secondary">
                            {post.price?.toLocaleString()}
                          </Typography>
                        </Box>
                      </Stack>
                    </Box>
                  )}

                  {/*  */}
                  <Typography 
                    variant="body2" 
                    color="text.secondary" 
                    sx={{ 
                      display: '-webkit-box',
                      WebkitLineClamp: 2,
                      WebkitBoxOrient: 'vertical',
                      overflow: 'hidden',
                      mb: 2
                    }}
                  >
                    {post.description}
                  </Typography>

                  {/*   */}
                  <Stack direction="row" spacing={2} alignItems="center" sx={{ color: 'text.secondary' }}>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <PersonIcon sx={{ fontSize: 16, mr: 0.5 }} />
                      <Typography variant="caption">
                        {post.authorName} ({post.authorGender})
                      </Typography>
                    </Box>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <PhoneIcon sx={{ fontSize: 16, mr: 0.5 }} />
                      <Typography variant="caption">
                        {post.authorPhone?.slice(0, -4)}****
                      </Typography>
                    </Box>
                  </Stack>
                </CardContent>

                <CardActions sx={{ p: 2, pt: 0 }}>
                  <Typography variant="caption" color="text.secondary">
                    {new Date(post.createdAt).toLocaleDateString()}
                  </Typography>
                  <Box sx={{ ml: 'auto' }}>
                    <Typography variant="caption" color="text.secondary">
                       {post.views}
                    </Typography>
                  </Box>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>

        {/*     */}
        {filteredPosts.length === 0 && !loading && (
          <Box sx={{ textAlign: 'center', py: 8 }}>
            <Typography variant="h6" color="text.secondary">
                 
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                 
            </Typography>
          </Box>
        )}

        {/*  */}
        {totalPages > 1 && (
          <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
            <Pagination
              count={totalPages}
              page={currentPage}
              onChange={handlePageChange}
              color="primary"
              size="large"
            />
          </Box>
        )}

        {/*    */}
        <Box sx={{ textAlign: 'center', mt: 4 }}>
          <Button 
            variant="outlined" 
            onClick={() => navigate('/')}
          >
              
          </Button>
        </Box>
      </Container>
    </AppTheme>
  );
}